import { TextField } from "@mui/material";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import { getCredForUser, loginWithCredential } from "./webauthn";
import { useForm } from "react-hook-form";
import { useState } from "react";
import Collapse from "@mui/material/Collapse";
import Alert from "@mui/material/Alert";
import { type LoginInfo, loginSchema } from "./schemaUtils";
import { zodResolver } from "@hookform/resolvers/zod";
import { getData, getErrorMsg } from "./restfullUtils";

function Login() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<LoginInfo>({
    resolver: zodResolver(loginSchema),
  });
  const [err, setErr] = useState<string | null>(null);
  const [info, setInfo] = useState<string | null>(null);

  async function handleLogin(data: LoginInfo) {
    setErr(null);
    setInfo("Passkey...");
    try {
      const credential = await getCredForUser(data.userName);
      setInfo(" Passkey, ...");
      const r = await loginWithCredential(credential);
      setInfo(`: ${r.userName}`);
    } catch (error) {
      setErr(error instanceof Error ? error.message : "");
    }
  }

  async function handleGoogleLogin() {
    window.location.href = `${
      import.meta.env.VITE_WEBAUTHN_HOST
    }/oauth2-login/google`;
  }

  async function handleGetUserInfo() {
    try {
      const u = await fetch(`${import.meta.env.VITE_WEBAUTHN_HOST}/auth/user`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
      });
      if (!u.ok) {
        throw new Error(`: ${await getErrorMsg(u)}`);
      }

      const user = await getData<{ userName: string; id: number }>(u);
      setInfo(`: ${user.userName}`);
    } catch (error) {
      setErr(error instanceof Error ? error.message : "");
    }
  }

  async function handleRefreshToken() {
    try {
      const response = await fetch(
        `${import.meta.env.VITE_WEBAUTHN_HOST}/token/refresh`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
        }
      );
      if (!response.ok) {
        throw new Error(`Token: ${await getErrorMsg(response)}`);
      }

      const r = await getData<{ msg: string }>(response);

      setInfo(`Token: ${r.msg}`);
    } catch (error) {
      setErr(error instanceof Error ? error.message : "");
    }
  }

  return (
    <Stack
      component="form"
      alignItems="center"
      justifyContent="center"
      sx={{ height: "100vh" }}
      onSubmit={handleSubmit(handleLogin)}
    >
      <h1></h1>
      <TextField
        label=""
        variant="outlined"
        margin="normal"
        fullWidth
        placeholder=""
        error={!!errors.userName}
        helperText={errors.userName ? errors.userName.message : ""}
        {...register("userName")}
      />
      <Button variant="contained" color="primary" type="submit">
        
      </Button>
      <Collapse in={!!err} sx={{ width: "100%", marginTop: 2 }}>
        <Alert
          severity="error"
          onClose={() => setErr(null)}
          sx={{ wordBreak: "break-all" }}
        >
          {err}
        </Alert>
      </Collapse>
      <Collapse in={!!info} sx={{ width: "100%", marginTop: 2 }}>
        <Alert severity="info" onClose={() => setInfo(null)}>
          {info}
        </Alert>
      </Collapse>
      <Button variant="contained" color="primary" onClick={handleGoogleLogin}>
        Google
      </Button>
      <Button
        variant="contained"
        color="primary"
        onClick={handleGetUserInfo}
        sx={{ marginTop: 2 }}
      >
        
      </Button>
      <Button
        variant="contained"
        color="primary"
        onClick={handleRefreshToken}
        sx={{ marginTop: 2 }}
      >
        Token
      </Button>
    </Stack>
  );
}

export default Login;
